.page {
 margin: 10px;
}

.list {
      border: 1px solid #a349a4;
      border-radius: 0.8rem;
      list-style: none;
      margin: 1rem;
      padding: 0.5rem;
      text-align: center;
      &__chraracter {
          color: #a349a4;
          font-family: Verdana, Geneva, Tahoma, sans-serif;
          padding: 5px;
        }
}


.new-phrase {
  margin-left: 30px;
  &__title {
   
    padding: 3rem 0 1rem 0;
    font-size: 1.3rem;
    color: #a349a4;
  }

  &__input {
    margin-right: 1rem;
    margin-bottom: 1rem;
    padding: 0.5rem;
    width: 8rem;
    border: 2px solid #ddd;
    border-radius: 0.5rem;
    background-color: #f8f8f8;
    outline: none;
    transition: all ease 0.3s;

    &:focus {
      border-color: #a349a4;
      background-color: #fff;
      box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    }
  }

  &__btn {
    padding: 0.5rem 1.5rem;
    border-radius: 0.5rem;
    border: none;
    border: 2px solid #a349a4;
    background-color: #a349a4;
    font-weight: bold;
    color: #fff;
    cursor: pointer;
    transition: all ease 0.3s;

    &:hover {
      border-color: #a349a4;
      background-color: #a349a4;
    }
  }
}



  

 